import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import PosSalesInvoiceUpdateForm from "./updateForm";

import notification from "../../../../components/notification";
import { Spin, Tooltip } from "antd";

import Button from "../../../../components/uielements/button";
import HistoryPosSalesInvoice from "./history";
import IntlMessages from "../../../../components/utility/intlMessages";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import Print from "./print";
import actions from "../../../../redux/pos/posSalesInvoice/actions";
import { Box } from "../../../App/app.style";
import { removeSaveData } from "../../../../helpers/helper";

const redirecTo = "/dashboard/pos/sales-invoices";

class UpdatePosSalesInvoice extends Component {
  state = {
    visibleModalHistory: false
  };
  componentDidMount() {
    const { editPosSalesInvoice } = this.props;
    const { id } = this.props.match.params;
    editPosSalesInvoice(id);
    // getTransactions(1, null, null);
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.posSalesInvoice.error) {
      notification("error", this.props.posSalesInvoice.error);
      this.props.clearError();
    }
    if (this.props.posSalesInvoice.success) {
      notification("success", this.props.posSalesInvoice.success);
      this.props.clearSuccess();
    }
    if (this.props.posSalesInvoice.warning) {
      notification("warning", this.props.posSalesInvoice.warning);
      this.props.clearSuccess();
    }
  }

  handleModalHistory = () => {
    const { visibleModalHistory } = this.state;
    visibleModalHistory
      ? this.setState({ visibleModalHistory: false })
      : this.setState({ visibleModalHistory: true });
  };

  handleSubmit = value => {
    const { id } = this.props.match.params;
    const { updatePosSalesInvoice } = this.props;
    updatePosSalesInvoice(id, value);
  };

  handleSubmitCancelSI = value => {
    const { cancelPosSalesInvoice } = this.props;
    const { id } = this.props.match.params;

    cancelPosSalesInvoice(id, value);
  };

  render() {
    const { loading, posSalesInvoice, printData } = this.props.posSalesInvoice;
    const modified = { ...posSalesInvoice, ...printData };

    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            marginBottom={true}
            title={<IntlMessages id="posSalesInvoices.button.update" />}
          >
            <div
              style={{
                alignItems: "center",
                display: "flex"
              }}
            >
              <Tooltip placement="top" title="Info">
                <Button
                  style={{
                    border: 0,
                    fontSize: "24px",
                    paddingTop: "2px"
                    // color: theme.palette.warning
                  }}
                  icon="issues-close"
                  onClick={() => this.handleModalHistory()}
                  loading={loading}
                >
                  {/* <IntlMessages id="itemRequests.history" /> */}
                </Button>
              </Tooltip>

              <Print
                loading={loading}
                data={modified}
                posSalesInvoice={this.props.posSalesInvoice}
              />
              <Button
                onClick={() => {
                  removeSaveData("si");
                  this.props.history.replace(redirecTo);
                }}
                style={{ marginLeft: "5px" }}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>

          <HistoryPosSalesInvoice
            visible={this.state.visibleModalHistory}
            handleModalHistory={this.handleModalHistory}
            description={posSalesInvoice ? posSalesInvoice.description : null}
          />

          {loading ? (
            <div className="spinStyle">
              <Spin size="large" />
            </div>
          ) : (
            <PosSalesInvoiceUpdateForm
              handleSubmitCancelSI={this.handleSubmitCancelSI}
              submit={this.handleSubmit}
              posSalesInvoice={this.props.posSalesInvoice}
            />
          )}
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    posSalesInvoice: state.posSalesInvoice,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(
  UpdatePosSalesInvoice
);
